/**
 *  
 * */
export default {
  methods: {
    /**
     * 
     */
    _event_link(eventData) {
      return new Promise((resolve) => {
        if (eventData.url) {
          window.location.href = eventData.url;
        }
        resolve();
      });
    },
    /**
     * 
     * @private
     */
    _event_share() {
      return new Promise((resolve) => {
        window.alert("");
        resolve();
      });
    },
    /**
     * 
     * @private
     */
    _event_submitForm() {
      return new Promise((resolve) => {
        window.alert("...");
        resolve();
      });
    },
    /**
     * WhatsApp
     * @private
     */
    _event_whatsApp() {
      console.log("************************");
      return new Promise((resolve) => {
        console.log(this.pageData);
        // key
        if (sessionStorage.getItem(this.pageData.id)) {
          alert("WhatsApp");
        } else {
          const url = "https://wa.me/" + this.pageData.whatsApp.username; // WhatsApp
          window.open(url, "_blank");

          // sessionStorage
          sessionStorage.setItem(this.pageData.id, "true");
        }
        fetch("/quark/joinUs/" + this.pageData.id, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ username: this.pageData.whatsApp.username }),
        })
          .then((response) => response.text())
          .then((data) => console.log(data))
          .catch((error) => console.log("Error:", error));
        resolve();
      });
    },
  },
};
